"use client";
import React, { useState } from "react";
import {
  Search,
  Loader2,
  CheckCircle,
  AlertCircle,
  Brain,
  Lightbulb,
  Code,
  Database,
  Globe,
  Smartphone,
  Cpu,
  Palette,
  ArrowLeft,
  Clock,
  Users,
  Target,
  Zap,
  BookOpen,
  ExternalLink,
  Play,
  FileText,
  ChevronRight,
  Calendar,
  Layers,
  Settings,
} from "lucide-react";

export default function ProjectGeneratorApp() {
  const [projectIdea, setProjectIdea] = useState("");
  const [isLoading, setIsLoading] = useState(false);
  const [result, setResult] = useState(null);
  const [error, setError] = useState("");
  const [currentPage, setCurrentPage] = useState("input"); // 'input' or 'results'
  const [activeTab, setActiveTab] = useState("overview");

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (!projectIdea.trim()) return;

    setIsLoading(true);
    setError("");
    setResult(null);

    try {
      const response = await fetch("/api/generate-project", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ projectIdea }),
      });

      const data = await response.json();
      if (!response.ok) throw new Error(data.error || "Failed to generate project");

      setResult(data);
      setCurrentPage("results");
      setActiveTab("overview");
    } catch (err) {
      setError(err.message);
    } finally {
      setIsLoading(false);
    }
  };

  const handleBackToInput = () => {
    setCurrentPage("input");
    setResult(null);
    setError("");
  };

  const handleNewProject = () => {
    setCurrentPage("input");
    setResult(null);
    setProjectIdea("");
    setError("");
  };

  const projectTypes = [
    { icon: Globe, label: "Web App", example: "E-commerce platform with user authentication" },
    { icon: Smartphone, label: "Mobile App", example: "Fitness tracking app with social features" },
    { icon: Brain, label: "AI/ML Project", example: "Image recognition system for medical diagnosis" },
    { icon: Database, label: "Backend API", example: "RESTful API for inventory management" },
    { icon: Palette, label: "Design System", example: "Component library for enterprise applications" },
    { icon: Cpu, label: "Blockchain", example: "NFT marketplace with smart contracts" },
  ];

  const tabs = [
    { id: "overview", label: "Project Overview", icon: Target },
    { id: "roadmap", label: "Development Roadmap", icon: Calendar },
    { id: "tech", label: "Technology Stack", icon: Layers },
    { id: "resources", label: "Learning Resources", icon: BookOpen },
  ];

  // Input Page Component
  const InputPage = () => (
    <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8 sm:py-12">
      {/* Header */}
      <header className="text-center mb-10 sm:mb-16">
        <div className="inline-flex items-center gap-3 mb-4 sm:mb-6">
          <div className="p-3 bg-blue-600 rounded-xl">
            <Brain className="w-8 h-8 text-white" />
          </div>
          <h1 className="text-3xl sm:text-4xl font-bold text-gray-900">
            AI Project Generator
          </h1>
        </div>
        <p className="text-base sm:text-xl text-gray-600 max-w-2xl mx-auto leading-relaxed">
          Transform your ideas into actionable project plans with comprehensive tech stacks, roadmaps, and resources.
        </p>
      </header>

      {/* Project Input Form */}
      <div className="max-w-4xl mx-auto mb-10 sm:mb-12">
        <div className="space-y-6">
          <div className="bg-white rounded-2xl shadow-sm border border-gray-200 p-6 sm:p-8">
            <label
              htmlFor="projectIdea"
              className="block text-lg font-semibold text-gray-900 mb-3 sm:mb-4"
            >
              Describe your project idea
            </label>
            <div className="relative">
              <textarea
                id="projectIdea"
                aria-label="Describe your project idea"
                value={projectIdea}
                onChange={(e) => setProjectIdea(e.target.value)}
                placeholder="e.g., A task management app with AI-powered priority suggestions..."
                className="w-full p-3 sm:p-4 border border-gray-300 rounded-xl resize-none focus:ring-2 focus:ring-blue-600 focus:border-transparent transition-all duration-200 text-base sm:text-lg"
                rows={4}
                maxLength={1000}
                disabled={isLoading}
              />
              <div className="absolute bottom-3 right-4 text-xs sm:text-sm text-gray-400">
                {projectIdea.length}/1000
              </div>
            </div>

            <button
              type="submit"
              disabled={isLoading || !projectIdea.trim()}
              className="mt-4 sm:mt-6 w-full sm:w-auto px-6 sm:px-8 py-3 bg-blue-600 text-white font-medium rounded-xl hover:bg-blue-700 focus:ring-2 focus:ring-blue-600 focus:ring-offset-2 disabled:opacity-50 disabled:cursor-not-allowed transition-all duration-200 inline-flex items-center justify-center gap-2 min-h-[44px]"
            >
              {isLoading ? (
                <>
                  <Loader2 className="w-5 h-5 animate-spin" aria-hidden="true" />
                  Generating Project Plan...
                </>
              ) : (
                <>
                  <Search className="w-5 h-5" aria-hidden="true" />
                  Generate Project Plan
                </>
              )}
            </button>
          </div>
        </div>

        {/* Project Type Examples */}
        <section className="mt-6 sm:mt-8">
          <h3 className="text-lg font-semibold text-gray-900 mb-3 sm:mb-4">
            Popular project types
          </h3>
          <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-3 sm:gap-4">
            {projectTypes.map((type, index) => (
              <button
                key={index}
                onClick={() => setProjectIdea(type.example)}
                className="text-left p-4 bg-white rounded-xl border border-gray-200 hover:border-blue-300 hover:shadow-sm transition-all duration-200 group min-h-[44px]"
              >
                <div className="flex items-center gap-3 mb-1 sm:mb-2">
                  <type.icon className="w-5 h-5 text-blue-600" aria-hidden="true" />
                  <span className="font-medium text-gray-900">{type.label}</span>
                </div>
                <p className="text-sm text-gray-600 group-hover:text-gray-700">
                  {type.example}
                </p>
              </button>
            ))}
          </div>
        </section>
      </div>

      {/* Error State */}
      {error && (
        <div className="max-w-4xl mx-auto mb-6 sm:mb-8" role="alert" aria-live="polite">
          <div className="bg-red-50 border border-red-200 rounded-xl p-4 flex items-center gap-3">
            <AlertCircle className="w-5 h-5 text-red-600 flex-shrink-0" aria-hidden="true" />
            <p className="text-red-800">{error}</p>
          </div>
        </div>
      )}
    </div>
  );

  // Results Page Component
  const ResultsPage = () => (
    <div className="min-h-screen bg-gray-50">
      {/* Header with Navigation */}
      <header className="bg-white border-b border-gray-200 sticky top-0 z-50">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-4">
          <div className="flex items-center justify-between">
            <div className="flex items-center gap-4">
              <button
                onClick={handleBackToInput}
                className="inline-flex items-center gap-2 px-3 py-2 text-sm font-medium text-gray-600 hover:text-gray-900 transition-colors duration-200"
              >
                <ArrowLeft className="w-4 h-4" />
                Back to Generator
              </button>
              <div className="h-6 w-px bg-gray-300"></div>
              <div className="inline-flex items-center gap-2">
                <Brain className="w-6 h-6 text-blue-600" />
                <h1 className="text-lg font-semibold text-gray-900">Project Plan</h1>
              </div>
            </div>
            <button
              onClick={handleNewProject}
              className="inline-flex items-center gap-2 px-4 py-2 bg-blue-600 text-white font-medium rounded-lg hover:bg-blue-700 transition-colors duration-200"
            >
              <Zap className="w-4 h-4" />
              New Project
            </button>
          </div>
        </div>
      </header>

      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-6 sm:py-8">
        {/* Success Banner */}
        <div className="bg-green-50 border border-green-200 rounded-xl p-4 mb-6 flex items-center gap-3">
          <CheckCircle className="w-5 h-5 text-green-600 flex-shrink-0" />
          <div>
            <p className="text-green-800 font-medium">Project plan generated successfully!</p>
            <p className="text-green-700 text-sm">Your comprehensive development guide is ready.</p>
          </div>
        </div>

        {/* Project Title Section */}
        <div className="bg-white rounded-2xl shadow-sm border border-gray-200 p-6 sm:p-8 mb-6">
          <div className="flex items-start gap-4">
            <div className="p-3 bg-blue-100 rounded-xl">
              <Target className="w-6 h-6 text-blue-600" />
            </div>
            <div className="flex-1">
              <h2 className="text-2xl sm:text-3xl font-bold text-gray-900 mb-2">
                {result?.mindMap?.name || "Your Project Plan"}
              </h2>
              <p className="text-gray-600 text-base sm:text-lg leading-relaxed">
                {result?.mindMap?.description || "Complete development roadmap and technical specifications for your project idea."}
              </p>
            </div>
          </div>
        </div>

        {/* Tab Navigation */}
        <div className="bg-white rounded-2xl shadow-sm border border-gray-200 overflow-hidden">
          <div className="border-b border-gray-200 overflow-x-auto">
            <nav className="flex min-w-max sm:min-w-0" role="tablist">
              {tabs.map((tab) => (
                <button
                  key={tab.id}
                  onClick={() => setActiveTab(tab.id)}
                  className={`flex items-center gap-3 px-6 py-4 text-sm font-medium border-b-2 transition-all duration-200 whitespace-nowrap ${
                    activeTab === tab.id
                      ? "border-blue-600 text-blue-600 bg-blue-50"
                      : "border-transparent text-gray-500 hover:text-gray-700 hover:bg-gray-50"
                  }`}
                  role="tab"
                  aria-selected={activeTab === tab.id}
                >
                  <tab.icon className="w-5 h-5" />
                  <span>{tab.label}</span>
                </button>
              ))}
            </nav>
          </div>

          {/* Tab Content */}
          <div className="p-6 sm:p-8">
            {/* Overview Tab */}
            {activeTab === "overview" && (
              <div className="space-y-8">
                <div>
                  <h3 className="text-xl font-bold text-gray-900 mb-4 flex items-center gap-2">
                    <Target className="w-5 h-5" />
                    Project Components
                  </h3>
                  {result?.mindMap?.children ? (
                    <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
                      {result.mindMap.children.map((component, index) => (
                        <div key={index} className="bg-gradient-to-br from-gray-50 to-gray-100 rounded-xl p-6 border border-gray-200">
                          <h4 className="text-lg font-semibold text-gray-900 mb-4 flex items-center gap-2">
                            <div className="w-2 h-2 bg-blue-600 rounded-full"></div>
                            {component.name}
                          </h4>
                          {component.children && (
                            <div className="space-y-3">
                              {component.children.map((subComponent, subIndex) => (
                                <div
                                  key={subIndex}
                                  className="flex items-center gap-3 text-gray-700 bg-white rounded-lg p-3 border border-gray-100"
                                >
                                  <ChevronRight className="w-4 h-4 text-blue-500" />
                                  <span className="font-medium">{subComponent.name}</span>
                                </div>
                              ))}
                            </div>
                          )}
                        </div>
                      ))}
                    </div>
                  ) : (
                    <div className="text-center py-12 bg-gray-50 rounded-xl">
                      <Target className="w-12 h-12 text-gray-400 mx-auto mb-4" />
                      <p className="text-gray-500">Project overview will appear here</p>
                    </div>
                  )}
                </div>
              </div>
            )}

            {/* Roadmap Tab */}
            {activeTab === "roadmap" && (
              <div className="space-y-8">
                <div>
                  <h3 className="text-xl font-bold text-gray-900 mb-4 flex items-center gap-2">
                    <Calendar className="w-5 h-5" />
                    Development Timeline
                  </h3>
                  {result?.roadmap?.length > 0 ? (
                    <div className="space-y-6">
                      {result.roadmap.map((phase, index) => (
                        <div key={index} className="relative">
                          <div className="bg-white border-2 border-gray-100 rounded-xl p-6 hover:border-blue-200 transition-colors duration-200">
                            <div className="flex items-start gap-4">
                              <div className="flex-shrink-0">
                                <div className="w-10 h-10 bg-gradient-to-br from-blue-500 to-blue-600 rounded-full flex items-center justify-center text-white font-bold">
                                  {index + 1}
                                </div>
                              </div>
                              <div className="flex-1">
                                <h4 className="text-lg font-semibold text-gray-900 mb-2">
                                  {phase.phase}
                                </h4>
                                <p className="text-gray-700 mb-4 leading-relaxed">{phase.description}</p>
                                {phase.tasks && (
                                  <div className="space-y-3">
                                    {phase.tasks.map((task, taskIndex) => (
                                      <div
                                        key={taskIndex}
                                        className="flex items-start gap-3 text-sm text-gray-600 bg-gray-50 rounded-lg p-3"
                                      >
                                        <CheckCircle className="w-4 h-4 text-green-500 flex-shrink-0 mt-0.5" />
                                        <span>{task}</span>
                                      </div>
                                    ))}
                                  </div>
                                )}
                              </div>
                            </div>
                          </div>
                          {index < result.roadmap.length - 1 && (
                            <div className="absolute left-5 top-20 w-0.5 h-8 bg-gradient-to-b from-blue-300 to-gray-300"></div>
                          )}
                        </div>
                      ))}
                    </div>
                  ) : (
                    <div className="text-center py-12 bg-gray-50 rounded-xl">
                      <Calendar className="w-12 h-12 text-gray-400 mx-auto mb-4" />
                      <p className="text-gray-500">Development roadmap will appear here</p>
                    </div>
                  )}
                </div>
              </div>
            )}

            {/* Tech Stack Tab */}
            {activeTab === "tech" && (
              <div className="space-y-8">
                <div>
                  <h3 className="text-xl font-bold text-gray-900 mb-4 flex items-center gap-2">
                    <Layers className="w-5 h-5" />
                    Technology Stack
                  </h3>
                  {result?.techStack ? (
                    <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
                      {Object.entries(result.techStack).map(([category, technologies]) => (
                        <div key={category} className="bg-gradient-to-br from-white to-gray-50 rounded-xl p-6 border-2 border-gray-100">
                          <h4 className="text-lg font-semibold text-gray-900 mb-4 capitalize flex items-center gap-2">
                            <Settings className="w-5 h-5 text-blue-600" />
                            {category.replace(/([A-Z])/g, " $1").trim()}
                          </h4>
                          <div className="space-y-2">
                            {Array.isArray(technologies)
                              ? technologies.map((tech, index) => (
                                  <div
                                    key={index}
                                    className="bg-white border border-gray-200 px-3 py-2 rounded-lg text-sm font-medium text-gray-700 hover:border-blue-300 transition-colors duration-200"
                                  >
                                    {tech}
                                  </div>
                                ))
                              : (
                                <div className="bg-white border border-gray-200 px-3 py-2 rounded-lg text-sm font-medium text-gray-700">
                                  {technologies}
                                </div>
                              )}
                          </div>
                        </div>
                      ))}
                    </div>
                  ) : (
                    <div className="text-center py-12 bg-gray-50 rounded-xl">
                      <Layers className="w-12 h-12 text-gray-400 mx-auto mb-4" />
                      <p className="text-gray-500">Technology recommendations will appear here</p>
                    </div>
                  )}
                </div>
              </div>
            )}

            {/* Resources Tab */}
            {activeTab === "resources" && (
              <div className="space-y-8">
                <div>
                  <h3 className="text-xl font-bold text-gray-900 mb-4 flex items-center gap-2">
                    <BookOpen className="w-5 h-5" />
                    Learning Resources
                  </h3>

                  {/* Documentation */}
                  {result?.documentation?.length > 0 && (
                    <section className="mb-8">
                      <h4 className="text-lg font-semibold text-gray-900 mb-4 flex items-center gap-2">
                        <FileText className="w-5 h-5 text-blue-600" />
                        Documentation
                      </h4>
                      <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                        {result.documentation.map((doc, index) => (
                          <a
                            key={index}
                            href={doc.url}
                            target="_blank"
                            rel="noopener noreferrer"
                            className="group block p-4 bg-white border-2 border-gray-100 rounded-xl hover:border-blue-300 hover:shadow-lg transition-all duration-200"
                          >
                            <div className="flex items-start justify-between mb-2">
                              <h5 className="font-semibold text-gray-900 group-hover:text-blue-600">{doc.title}</h5>
                              <ExternalLink className="w-4 h-4 text-gray-400 group-hover:text-blue-600 flex-shrink-0" />
                            </div>
                            <p className="text-sm text-gray-600 leading-relaxed">{doc.description}</p>
                          </a>
                        ))}
                      </div>
                    </section>
                  )}

                  {/* YouTube Resources */}
                  {result?.youtubeResources?.length > 0 && (
                    <section>
                      <h4 className="text-lg font-semibold text-gray-900 mb-4 flex items-center gap-2">
                        <Play className="w-5 h-5 text-red-600" />
                        Video Tutorials
                      </h4>
                      <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                        {result.youtubeResources.map((video, index) => (
                          <a
                            key={index}
                            href={video.url}
                            target="_blank"
                            rel="noopener noreferrer"
                            className="group block p-4 bg-white border-2 border-gray-100 rounded-xl hover:border-red-300 hover:shadow-lg transition-all duration-200"
                          >
                            <div className="flex items-start justify-between mb-2">
                              <h5 className="font-semibold text-gray-900 group-hover:text-red-600">{video.title}</h5>
                              <Play className="w-4 h-4 text-gray-400 group-hover:text-red-600 flex-shrink-0" />
                            </div>
                            <p className="text-sm text-red-600 font-medium mb-1">{video.channel}</p>
                            <p className="text-sm text-gray-600 leading-relaxed">{video.description}</p>
                          </a>
                        ))}
                      </div>
                    </section>
                  )}

                  {/* Empty State */}
                  {(!result?.documentation || result.documentation.length === 0) && 
                   (!result?.youtubeResources || result.youtubeResources.length === 0) && (
                    <div className="text-center py-12 bg-gray-50 rounded-xl">
                      <BookOpen className="w-12 h-12 text-gray-400 mx-auto mb-4" />
                      <p className="text-gray-500">Learning resources will appear here</p>
                    </div>
                  )}
                </div>
              </div>
            )}
          </div>
        </div>
      </div>
    </div>
  );

  return (
    <div
      className="min-h-screen bg-gray-50"
      style={{ fontFamily: "'Sora', -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif" }}
    >
      {currentPage === "input" ? <InputPage /> : <ResultsPage />}
    </div>
  );
}